@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  background-image: linear-gradient(
      to top,
      transparentize(v.$primario, 0.1) 0%,
      transparentize(v.$primario, 0.1) 100%
    ),
    url(../img/header_bg.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
  position: relative;
  @include m.tablet {
    padding: 3rem 0 20rem 0;
  }

  &__barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  &__logo {
    width: 20rem;
  }

  &__contenido {
    @include m.tablet {
      margin-top: 10rem;
      max-width: 50rem;
    }
  }
  &__heading {
    color: v.$blanco;
    text-align: center;
    @include m.tablet {
      text-align: left;
    }
  }
  &__texto {
    color: v.$blanco;
    display: none;
    @include m.tablet {
      display: block;
    }
  }

  &__imagen {
    display: none;
    @include m.tablet {
      display: block;
      position: absolute;
      right: 3rem;
      bottom: -5rem;
      max-width: 35rem;
      border-radius: 1rem;
    }
    @include m.desktopXL {
        max-width: 60rem;
    }
  }
}
.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
  @include m.tablet {
    flex-direction: row;
    gap: 2rem;
    margin-top: 0;
  }
  &__link {
    color: v.$blanco;
    font-size: 1.8rem;
    margin-bottom: 2rem;
    &:last-of-type {
      margin-bottom: 0;
    }
    @include m.tablet {
      margin-bottom: 0;
    }
  }
}
